import React from 'react';
import 'isomorphic-fetch';
import { Button, FormGroup, FormControl, ControlLabel, Form, Row, Col, Nav, NavItem, ButtonToolbar, Tabs, Tab, TabContainer, TabContent, TabPane} from 'react-bootstrap';
import { SocketProvider } from 'socket.io-react';
import SocketIOClient from 'socket.io-client';
let socket;

export default class Diagnostics extends React.Component {
 
constructor(props){
    	super(props);
	this.state = {
		text: '',
		host: '127.0.0.1',
		port: 5250,
		command: "cls",
		response: '',
	}
	this.onHostChange = this.onHostChange.bind(this);
	this.onPortChange = this.onPortChange.bind(this);
	this.onCommandChange = this.onCommandChange.bind(this);
	this.sendTelnetTest = this.sendTelnetTest.bind(this);
}
componentDidMount() {
	socket = SocketIOClient();
	socket.on(this.props.location.pathname, (mesg) => {
		this.setState({text: mesg});
	});
	socket.on('telnet-response', (mesg) => {
		this.setState({response: mesg});
	});
}
componentWillUnmount() {
	socket.off(this.props.page);
}
onPortChange(event) {
    this.setState({ port: event.target.value });
 }
 onHostChange(event) {
    this.setState({ host: event.target.value });
 }
 onCommandChange(event) {
    this.setState({ command: event.target.value });
 }
sendTelnetTest() {
  console.log("sending Telnet Test");
  socket.emit('diagnostics-send-telnet', { host: this.state.host, port: this.state.port, command: this.state.command});
}
 render() {
 	return (
	<div>
		<Tab.Container id="tabs-with-dropdown" defaultActiveKey="first">
		  <Row className="clearfix">
		    <Col sm={12}>
		      <Nav bsStyle="tabs">
		        <NavItem eventKey="telnet-tab">Telnet</NavItem>
		        <NavItem eventKey="dmx-tab">DMX</NavItem>
		        <NavItem eventKey="midi-tab">MIDI</NavItem>
		        <NavItem eventKey="osc-tab">OSC</NavItem>
		        <NavDropdown eventKey="3" title="Dropdown" id="nav-dropdown-within-tab">
		          <MenuItem eventKey="3.1">Action</MenuItem>
		          <MenuItem eventKey="3.2">Another action</MenuItem>
		          <MenuItem eventKey="3.3">Something else here</MenuItem>
		          <MenuItem divider />
		          <MenuItem eventKey="3.4">Separated link</MenuItem>
		        </NavDropdown>
		      </Nav>
		    </Col>
		    <Col sm={12}>
		      <Tab.Content animation>
		        <Tab.Pane eventKey="telnet-tab">
					<div>
						<Form horizontal onSubmit={this.sendTelnetTest}>
				          <FormGroup>
				            <Col componentClass={ControlLabel} sm={3}>Host</Col>
				            <Col sm={9}>
				              <FormControl name="host" value={this.state.host} onChange={this.onHostChange} />
				            </Col>
				          </FormGroup>
				          <FormGroup>
				            <Col componentClass={ControlLabel} sm={3}>Port</Col>
				            <Col sm={9}>
				              <FormControl name="port" value={this.state.port} onChange={this.onPortChange} />
				            </Col>
				          </FormGroup>
				          <FormGroup>
				            <Col componentClass={ControlLabel} sm={3}>Telnet Command</Col>
				            <Col sm={9}>
				              <FormControl name="command" value={this.state.command} onChange={this.onCommandChange} />
				            </Col>
				          </FormGroup>
				          <FormGroup>
				            <Col smOffset={3} sm={6}>
				              <ButtonToolbar>
				                <Button bsStyle="primary" type="submit">Send Telnet Command</Button>
				              </ButtonToolbar>
				            </Col>
				          </FormGroup>
				        </Form>
					</div>
					<div>{this.state.response}</div>
		        </Tab.Pane>
		        <Tab.Pane eventKey="dmx-tab">Tab 2 content</Tab.Pane>
		        <Tab.Pane eventKey="midi-tab">Tab 2 content</Tab.Pane>
		        <Tab.Pane eventKey="osc-tab">Tab 2 content</Tab.Pane>
		        <Tab.Pane eventKey="3.1">Tab 3.1 content</Tab.Pane>
		        <Tab.Pane eventKey="3.2">Tab 3.2 content</Tab.Pane>
		        <Tab.Pane eventKey="3.3">Tab 3.3 content</Tab.Pane>
		        <Tab.Pane eventKey="3.4">Tab 3.4 content</Tab.Pane>
		      </Tab.Content>
		    </Col>
		  </Row>
		</Tab.Container>
		
	</div>
    );
  };
}




